<script>
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@/@core/utils/validations/validations'
import {
  BFormInput,
  BFormGroup,
  BForm,
  BRow,
  BCol,
  BFormSelect,
  BModal,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const dictionary = {
  ar: {
    names: {
      name: '',
      type: '',
      image: ' ',
      phone: '',
      email: ' ',
      address: '',
      password: ' ',
      employer_phone: ' ',
      employer_name: ' ',
      website: '',
      country: '',
      city: '',
      work_status: ' ',
      factory_register_image: '  ',
      category: '',
      status: '',
    },
  },
}
localize('ar', dictionary.ar)
export default {
  name: 'EditCategory',
  components: {
    ValidationProvider,
    ValidationObserver,
    BRow,
    BCol,
    BFormSelect,
    BForm,
    BFormGroup,
    BFormInput,
    BModal,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
  },
  props: {
    user: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      countryOptions: [],
      cityOptions: [],
      FreelancerCategories: [],
      workStatus: [
        {
          name: ' ',
          id: 'full_time',
        },
        {
          name: '',
          id: 'work',
        },
      ],
      required,
    }
  },
  computed: {
    userObj() {
      const { user } = this
      user.user_id = user.id
      user.lat = 10.323
      user.lng = 21.5445
      return user
    },
  },
  watch: {
    userObj(val) {
      this.changeGov(val.country_id)
    },
  },
  created() {
    this.fetchGov()
    this.fetchFreelancerCategories()
  },
  methods: {
    update() {
      this.$refs.accountRules.validate().then(success => {
        if (success) {
          this.$store.dispatch('freelancer/update', this.userObj).then(() => {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: '  ',
                icon: 'CheckCircleIcon',
                variant: 'success',
              },
            })
          })
        } else {
          return false
        }
        return true
      })
    },
    fileCategory(event) {
      // eslint-disable-next-line prefer-destructuring
      this.dataImage = event.target.files[0]
    },
    fetchGov() {
      this.$store.dispatch('common/FETCH_GOV').then(res => {
        this.countryOptions = res.data.data.governorates
      })
    },
    changeGov(event) {
      this.$store.dispatch('common/FETCH_CITY', event).then(res => {
        this.cityOptions = res.data.data.cities
      })
    },
    fetchFreelancerCategories() {
      this.$store.dispatch('common/fetchFreelancerCategories').then(res => {
        this.FreelancerCategories = res.data.data.categories
      })
    },
  },
}
</script>

